@import 'styles/variables.scss';
@import 'styles/media.scss';

body {
  position: relative;
}

.main-page {
  width: 100%;
  min-height: 100vh;
  padding-bottom: 50px;
  background: rgba(0, 0, 0, 0.1);
  overflow: hidden;

  &__background {
    width: 100%;
    position: absolute;
    height: 100%;
    object-fit: cover;
    z-index: -2;
  }

  &__block-image {
    margin: 0 auto;
    border-radius: 30px;
    position: relative;
    height: 400px;
    width: 400px;
  }

  &__image-cat {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    position: absolute;
  }

  &__liza-cat {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    position: absolute;

    &_showed {
      animation-name: show;
      animation-duration: 4s;
      animation-fill-mode: forwards;
    }
  }

  &__text {
    color: white;
    margin: 0 auto;
    display: flex;
    width: fit-content;
    align-items: center;
    margin-top: 90px;
    margin-bottom: 10px;
    font-size: 20px;
    font-family: Roboto;
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
