body {
  margin: 0;
  box-sizing: border-box;
}

div {
  box-sizing: border-box;
}

a {
  box-sizing: border-box;
  color: #000000;
  text-decoration: none;
  /* no underline */
}

span {
  box-sizing: border-box;
  display: inline-block;
}

button {
  box-sizing: border-box;
  border: none;
  background-color: #FFFF;
  padding: 0;
  outline: none;
}

input {
  outline: none;
  box-sizing: border-box;
}

img {
  box-sizing: border-box;
}

ul {
  margin: 0;
}


.jmak {
  position: absolute; 
  color: white;
  animation-name: show;
  animation-duration: 3s;
  font-family: Roboto;
  font-size: 18px;
}

@keyframes show {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}